import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

//引入各页面
import Home from '../pages/home.vue'
import Order from '../pages/order.vue'

export default new Router({
  routes: [
    { path:'/home',component:Home },
    { path:'/order',component:Order},
    { path:'/' , redirect:'/home'}
  ]
})



